// src/components/AddModal.js
import React, { useState, useEffect } from 'react';

// By defining the default object outside the component, we ensure it's a stable reference.
// This prevents the useEffect hook from re-running unnecessarily.
const EMPTY_INITIAL_STATE = {};

const AddModal = ({ isOpen, onClose, onSave, title, fields, initialState = EMPTY_INITIAL_STATE, showNotification }) => {
    const [formData, setFormData] = useState({});

    useEffect(() => {
        // This effect now correctly runs only when the modal is opened,
        // or if the `fields` or `initialState` props genuinely change.
        // Previously, a new `initialState` object was created on every render,
        // causing this effect to fire on every keystroke and reset the form data.
        if (isOpen) {
            const initialData = fields.reduce((acc, field) => {
                acc[field.name] = initialState[field.name] || field.defaultValue || '';
                return acc;
            }, {});
            setFormData(initialData);
        } else {
            // Reset formData when the modal closes to ensure a clean state for next time.
            setFormData({});
        }
    }, [isOpen, fields, initialState]);


    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSaveClick = () => {
        // Basic validation to ensure required fields are filled.
        for (const field of fields) {
            if (field.required && !formData[field.name]) {
                if (showNotification) {
                    showNotification(`${field.label} is required.`, 'error');
                } else {
                    console.error(`${field.label} is required.`);
                }
                return;
            }
        }
        onSave(formData);
        onClose(); // Close modal after saving
    };

    if (!isOpen) {
        return null;
    }

    return (
        <div className="fixed inset-0 bg-gray-600 bg-opacity-50 flex items-center justify-center p-4 z-50">
            <div className="bg-white rounded-lg shadow-xl p-6 w-full max-w-md mx-auto relative transform transition-all scale-100 opacity-100">
                <h2 className="text-2xl font-bold mb-4 text-gray-800">{title}</h2>
                <div className="space-y-4">
                    {fields.map(field => (
                        <div key={field.name} className="flex flex-col">
                            <label htmlFor={field.name} className="block text-sm font-medium text-gray-700 mb-1">
                                {field.label}{field.required && <span className="text-red-500">*</span>}
                            </label>
                            {field.type === 'textarea' ? (
                                <textarea
                                    id={field.name}
                                    name={field.name}
                                    placeholder={field.placeholder || ''}
                                    value={formData[field.name] || ''}
                                    onChange={handleChange}
                                    // The `disabled` property is correctly read from the field config.
                                    // Since `addModalFields` doesn't set it, this will be undefined, and the input will be enabled.
                                    disabled={field.disabled || false}
                                    rows="3"
                                    className="w-full px-4 py-2 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-gray-50 disabled:bg-gray-200"
                                />
                            ) : field.type === 'select' ? (
                                <select
                                    id={field.name}
                                    name={field.name}
                                    value={formData[field.name] || ''}
                                    onChange={handleChange}
                                    disabled={field.disabled || false}
                                    className="w-full px-4 py-2 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-gray-50 disabled:bg-gray-200"
                                >
                                    {field.options && field.options.map(option => (
                                        <option key={option.value} value={option.value}>
                                            {option.label}
                                        </option>
                                    ))}
                                </select>
                            ) : (
                                <input
                                    type={field.type || 'text'}
                                    id={field.name}
                                    name={field.name}
                                    placeholder={field.placeholder || ''}
                                    value={formData[field.name] || ''}
                                    onChange={handleChange}
                                    disabled={field.disabled || false}
                                    className="w-full px-4 py-2 rounded-lg border border-gray-200 focus:outline-none focus:ring-2 focus:ring-blue-500 bg-gray-50 disabled:bg-gray-200"
                                />
                            )}
                        </div>
                    ))}
                </div>
                <div className="mt-6 flex justify-end space-x-3">
                     <button
                        onClick={onClose}
                        className="px-4 py-2 bg-gray-200 text-gray-700 font-bold rounded-lg hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-400 transition duration-300"
                    >
                        Cancel
                    </button>
                    <button
                        onClick={handleSaveClick}
                        className="px-4 py-2 bg-blue-600 text-white font-bold rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 transition duration-300"
                    >
                        Save
                    </button>
                </div>
            </div>
        </div>
    );
};

export default AddModal;